/* Imports */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200;300;400&display=swap');


/* Global */
*,
*::after,
*::before {
box-sizing: border-box;
margin: 0;
padding: 0;
}


body {
height: 100vh;
width: 100%;
font-family: 'Inter', sans-serif;
font-weight: 200;
font-size: 1rem;
line-height: 1.5;
color: #FFF;
background: #222;
background-image: url("/src/assets/cinemax-bg.png");
background-position: center;
background-repeat: no-repeat;
background-size: cover;
overflow-x: hidden;
}


.App {
padding: 5rem 0;
/* max-width: 1000px; */
}


/* Fonts */
h1,
h2,
h3,
h4,
h5,
h6 {
font-family: 'Inter', sans-serif;
font-weight: 400;
}


a {
text-decoration: none;
color: #000;
}


a:visited {
color: #777;
}


a[target="_blank"] {
background-color: rgb(255, 255, 0);
padding: 0.25rem 0.5rem;
font-weight: 400;
border-radius: 1rem;
box-shadow: inset rgba(0, 0, 0, 0.2) 0 0 3px;
}


a[target="_blank"]::after {
content: "â†—";
margin-left: 0.25rem;
}


/* Header */
header {
display: grid;
place-items: center;
padding: 2rem;
}


.logo {
width: 320px;
padding-bottom: 2.5rem;
}


/* Formulaire */
.search {
padding: .15rem;
text-align: center;
}


input[type="text"] {
background-color: transparent;
border: none;
border-bottom: 1px solid #8e8e8e;
color: #FFFFFF;
font-size: 1.5rem;
font-weight: 300;
outline: none;
margin-bottom: 1.5rem;
padding: .5rem 1rem;
text-align: center;
width: 100%;
}


input[type="text"]:focus {
border-bottom: 1px solid #999999;
}


button {
align-items: center;
background-color: #FFFFFF;
border: 0;
box-shadow: rgba(0, 0, 0, 0.5) 0 1px 3px 0;
box-sizing: border-box;
cursor: pointer;
display: inline-flex;
font-size: .85rem;
font-weight: 200;
text-transform: uppercase;
justify-content: center;
letter-spacing: 1px;
margin: 0;
min-height: 3rem;
padding: calc(.875rem - 1px) calc(1.5rem - 1px);
position: relative;
text-decoration: none;
transition: all 250ms;
user-select: none;
-webkit-user-select: none;
touch-action: manipulation;
vertical-align: baseline;
width: auto;
}


button:hover,
button:focus {
border: 1px solid rgb(255, 255, 255);
background-color: transparent;
color: #FFFFFF;
letter-spacing: 2px;
}


button:hover {
transform: translateY(-1px);
}


button:active {
background-color: #F0F0F1;
border-color: rgba(0, 0, 0, 0.15);
box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
color: rgba(0, 0, 0, 0.65);
transform: translateY(0);
}


/* Card */
.movies {
max-width: 1200px;
margin: 0 auto;
display: flex;
flex-wrap: wrap;
justify-content: center;
gap: 2.5rem;
padding: 0 1rem;
}


.movie {
width: 320px;
text-align: center;
color: #000;
background-color: #FFFFFF;
transition: all 250ms ease-in-out;
}


.movie:hover {
transform: translateY(-10px);
transition: all 250ms ease-in-out;
}


.movie img {
width: 300px;
height: 450px;
object-fit: cover;
padding-top: .7rem;
}


.movie h3 {
font-size: 1.15rem;
font-weight: 300;
padding: .5rem 0;
}


.movie p {
font-size: .85rem;
font-weight: 200;
padding: .5rem 0;
}


